<template>
  <div>
    <div class="search-box mt20">
      <div class="field">
        <label>用户ID</label>
        <el-input v-model.trim="uid" placeholder="请输入用户ID" class="width200"></el-input>
      </div>
      <div class="field">
        <label>身份</label>
        <el-select v-model="type" placeholder="请选择">
          <el-option
            v-for="item in identifyType"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <!-- <div class="field">
        <label>状态</label>
        <el-select v-model="status" placeholder="请选择">
          <el-option
            v-for="item in statusList"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div> -->
      <div class="field">
        <el-button type="primary" @click="search">查询</el-button>
        <el-button @click="reset">重置</el-button>
      </div>
    </div>
    <div class="align-right">
      <el-button type="primary" :disabled="!exportIds">
        <a :href="exportInfoUrl" style="color: #fff" v-if="!!exportIds">批量导出信息</a>
        <span v-else>批量导出信息</span>
      </el-button>
      <el-button type="primary" :disabled="!exportIds">
        <a :href="exportQrcodeUrl" style="color: #fff" v-if="!!exportIds">批量导出二维码</a>
        <span v-else>批量导出二维码</span>
      </el-button>
    </div>
    <el-table
      class="mt10"
      border
      :data="listData"
      v-loading="loading"
      @selection-change="handleSelectionChange">
      <!-- <el-table-column type="selection" width="55" :selectable='checkboxInit'></el-table-column> -->
      <el-table-column prop="uid" label="用户ID"></el-table-column>
      <el-table-column prop="cardName" label="名片名称"></el-table-column>
      <el-table-column prop="accountMobile" label="手机号(用户账号)"></el-table-column>
      <el-table-column prop="type" label="身份">
        <template slot-scope="scope">
          <span>{{scope.row.type | filterType}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="qrcode" label="名片二维码">
        <template slot-scope="scope">
          <div class="co_img" @click="showBigQrcode(scope.row.qrcode)" v-if="scope.row.qrcode"
            :style="{background:'url('+scope.row.qrcode+') center/cover'}"></div>
        </template>
      </el-table-column>
      <el-table-column prop="updateTime" label="用户身份更新时间"></el-table-column>
      <!-- <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="userOperate(scope.row, 1)" v-if="scope.row.productShelves === 1">启用</el-button>
          <el-button type="text" size="small" @click="userOperate(scope.row, 2)" v-if="scope.row.productShelves === 2">停用</el-button>
        </template>
      </el-table-column> -->
    </el-table>
    <div class="pag-block" v-if="listData.length> 0">
      <div class="fl">每页{{pageSize}}条，共 {{total}} 条记录 第 {{page}}/{{total}} 页</div>
      <el-pagination
        class="fr"
				@current-change="getListbyPage"
			  :current-page="page"
			  background
			  layout="prev, pager, next, jumper"
			  :page-size="pageSize"
			  :total="total">
			</el-pagination>
    </div>
    <!-- 二维码dialog -->
    <el-dialog
      :title="dialogTtitle"
      :visible.sync="dialogVisible"
      width="680px">
      <img :src="qrcodeimg" alt="" class="qrcodeimg">
    </el-dialog>
  </div>
</template>

<script>
import api from '@/api'
import { mapGetters } from 'vuex';
import typesOfRepresent from '@/assets/js/typesOfRepresent'

export default {
  components: {

  },
  watch: {

  },
  data() {
    return {
      uid: '',
      type: null,
      identifyType: typesOfRepresent.identifyType,
      // statusList: [{label: '启用', value: 1}, {label: '停用', value: 0}],
      // status: null,
      list: [],
      listData: [],
      loading: false,
      multipleSelection: [],
      page: 1,
      pageSize: 10,
      total: 0,
      dialogTtitle: '二维码',
      dialogVisible: false,
      qrcodeimg: '',
      exportIds: ''
    };
  },
  computed: {
    // 导出二维码
    exportQrcodeUrl() {
      return api.usersExportQrcodeApi + '?ids=' + this.exportIds
    },
    // 导出用户信息
    exportInfoUrl() {
      return api.usersExportInfoApi + '?ids=' + this.exportIds
    }
  },
  reset() {
    this.uid = '';
    this.type = null;
  },
  created() {
    this.getList();
  },
  mounted() {
    
  },
  methods: {
    // 没有qrcode不能导出
    checkboxInit(row, index){
      if (!row.qrcode) {
        return 0;//不可勾选
      }
      else {
        return 1;//可勾选
      }
    },
    // 是否可以导出
    isExport() {
      if (this.uid || [1, 2, 3].indexOf(Number(this.type)) >= 0) {
        return true
      } else {
        return false
      }
    },
    getList() {
      this.loading = true
			this.$axios.post(api.userListApi, {
        page: this.page -1,
        page_size: this.pageSize,
        type: this.type,
        uid: this.uid,
        is_export: this.isExport()
        // status: this.status
      }).then(res => {
        this.loading = false;
        this.listData = ((res.data.result || {}).list) || []
        this.total = Number((res.data.result || {}).total || 0)
        this.exportIds = (res.data.result || {}).ids || ''
			});
    },
    search() {
      if (this.uid.trim() != '' && (isNaN(this.uid) || Number(this.uid) <= 0)) {
        this.$message.warning('用户ID请输入大于0的数字')
        return;
      }
      this.page = 1;
      this.getList();
    },
    reset() {
      this.status = null;
      this.uid = '';
      this.type = null;
    },
    // qrcode大图
    showBigQrcode(qrcode) {
      if (qrcode) {
        this.qrcodeimg = qrcode;
        this.dialogVisible = true;
      }
    },
    // 停用，启用
    // userOperate(row, type) {
    //   this.$axios.get(api.userOperateApi, { params: {
    //     uid: row.uid,
		// 		type: type
		// 	}}).then(res => {
    //     this.$message.success('操作成功！');
    //     this.getList();
    //   });
    // },
    //监听多选变更
		handleSelectionChange(val) {
			this.multipleSelection = [];
			for (var i = 0; i < val.length; i++) {
				this.multipleSelection.push(val[i].uid);
			}
    },
    getListbyPage(page) {
			this.page = page;
			this.getList();
		},
  },
  filters: {
    filterType(value) {
      return typesOfRepresent.getLabelByValue(typesOfRepresent.identifyType, value);
    }
  }
};
</script>

<style lang="scss" scoped>
.co_img {
	width: 60px;
	height: 60px;
	margin: auto;
  cursor: pointer;
}
.qrcodeimg{
  width: 640px;
  height: 640px;
}
</style>



